* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative; }

nav {
    height: 50px;
    background-color: #212121; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 12px 70px;
    margin: 0;
    list-style: none; }

.menu_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300; }

.menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; }

.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6); }

.subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

.subheader_official {
    margin-top: 6px;
    font-size: 13px;
    font-weight: 400;
    opacity: 0.5;
    color: #ffffff; }

.subheader_call {
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right;
    color: #ffffff; }

.subheader_phone {
    display: block;
    font-size: 22px;
    font-weight: 400;
    margin-top: 8px;
    text-align: right;
    text-transform: uppercase;
    line-height: 12px;
    color: #ffffff; }

.subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: 0px -3px 0px 0px #0000001F inset;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff; }

.promo {
    min-height: 800px;
    background: url('../img/bg/uber_bg.png') center (center / cover) no-repeat;
    padding: 150px 0 135px 0; }

.promo_header {
    font-size: 60px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 60px;
    text-align: center;
    color: #ffffff; }

.promo_subheader {
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center;
    color: #ffffff; }

.promo_desc {
    font-size: 17px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px;
    color: #ffffff; }

.promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    border: none;
    border-radius: 4px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: 0px -4px 0px 0px #0000001F inset;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    color: #ffffff; }

.reasons {
    padding: 71px 0 65px 0; }

.label {
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase; }

.title {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }

.reasons .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

.reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: #1eacc7;
    border-radius: 100%; }

.reasons_descr {
    width: 343px;

    .reasons_subtitle {
        line-height: 21px;
        color: #1eacc7;
        font-size: 17px;
        font-weight: 400; }

    .reasons_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }
